

.formulario {
    &__header{
        &--btn-volver {
            font-size: 2rem;
        }
    }

    &__body{
        &--titulo {
            font-size: 1.8rem;
            font-weight: 400;
            margin-bottom: 2rem;
            text-align: center;
        }

        &__campo{
     
            padding: 0;

            &--label {
                font-size: 2.5rem;
                margin-left: 0;

            }

            &--input {
                font-size: 1.5rem;
                margin: 0;
            }
        }
    }

    &__footer {
        &--btn-registrar{
            font-size: 2rem;
        }
    }
    
}
